.cards {
  background-color: aqua;
  border: 2px solid black;
}


body {
  text-align: center;
  background-image: url(https://wallpapermemory.com/uploads/791/sword-art-online-2-ii-wallpaper-hd-2560x1440-112804.jpg);
}


.grid-container > div {
  background-color: rgba(0, 255, 255, 0.8);
  text-align: center;
  padding: 20px 0;
  font-size: 10px;
}


nav {
  list-style-type: none;
  margin: 0%;
  padding: 0%;
  overflow: hidden;
  background-color: #333;
}


nav a {
  display: block;
  color: white;
  text-align: center;
  padding: 1.5% 4.5%;
  text-decoration: none;
  float: left;
}


nav a:hover:not(.active) {
  background-color: #111;
}


.anime {
  padding: 10px;
  width: 230px;
  margin: 40px;
  display: grid;
  grid-template-columns: auto auto auto auto;
  grid-gap: 100px;
  padding: 100px 200px;
  justify-content: center;
  margin-left: 630px;
  margin-top: 0;
}


.favecards {
  border: black solid 2px;
  margin: 0 700px;
  background-color: rgba(205, 255, 247, 0.5);
}


button {
  background-color: rgb(205, 255, 247);
  font-size: 20px;
  box-shadow: rgb(255, 220, 229);
}


.buttonAdd button {
  width: 100%;
}


input[type="checkbox"] {
  width: 25px;
  height: 25px;
  background: white;
  border-radius: 5px;
  border: 2px solid rgb(0, 0, 0);
}
